body {
    font-family: 'vazirmatn', sans-serif;
}

section {
    padding: 0px 0px;
    margin: 20px 10px;
    background-color: #f4f4f4;
    border: 1px solid #ddd;
    border-radius: 4px;
}
section p{
   font-size: medium;
}
p {
    text-align:justify !important;
  }

  .link {
    cursor: pointer;
    color: blue;
    text-decoration: underline;
  }
  .link:hover {
    text-decoration: none;
  }
  
  .back-link {
    position: absolute; /* Position dynamically near the target */
    z-index: 1000;
  }
  
  /* Header styles */
.header {
    background-color: black;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  .header h1 {
    font-family: 'vazirmatn',sans-serif;
    margin: 0;
    font-size: 1.2em;
    text-align: center;
  }
  
  /* Hamburger menu */
  .menu-icon {
    display: block;
    font-size: 1.5em;
    cursor: pointer;
    position: relative; /* Needed to position menu relative to icon */
  }
  
  /* Navigation menu */
  .nav-menu {
    display: none;
    flex-direction: column;
    background-color: black;
    position: absolute;
    top: 100%; /* Position below the hamburger icon */
    right: 0; /* Align to the right of the header */
    z-index: 999;
    list-style: none;
    padding: 10px 0;
    margin: 0;
    white-space: nowrap; /* Prevent wrapping of menu items */
    width: auto; /* Dynamically adjust width based on content */
    min-width: 250px; /* Ensure the width matches the longest menu item */
  }
  
  .nav-menu.active {
    display: flex;
  }
  
  .nav-menu li {
    margin: 10px 0;
    text-align: right; /* Justify text to the right */
    padding: 0 10px; /* Add padding for better appearance */
  }
  
  .nav-menu a {
    text-decoration: none;
    color: white;
    font-size: 1em;
    transition: color 0.3s;
    display: block; /* Ensure full clickable area */
  }
  
  .nav-menu a:hover {
    color: #00bcd4;
  }

  section h1 {
    margin: 0 0;
    font-size: larger;
    font-weight: bold;
}
.spinner-overlay {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
