/* styles.css or component's CSS file */
::ng-deep  .mat-mdc-simple-snack-bar {
  font-family: 'vazirmatn', sans-serif; /* Set the font */
  font-size: 16px; /* Customize the font size */
  text-align: center; /* Center-align text */
  color: white; /* Text color */
  background-color: #f44336; /* Background color */
}
::ng-deep .success-snackbar {
  font-family: 'vazirmatn', sans-serif; /* Set the font */
  font-size: 16px; /* Customize the font size */
  text-align: center; /* Center-align text */
  color: white; /* Text color */
  background-color: green; /* Background color */
}
/* Center the snackbar */
::ng-deep .mat-snack-bar-container {
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  display: flex; /* Required for centering */
  min-width: 100px; /* Optional: avoid very narrow snackbars */
  max-width: none; /* Allow full-width snackbars */
  margin: 0 auto; /* Center horizontally in the section */
}
